import * as React from 'react';
import Loading from '../../shared/Loading';
import ArticlesListItem from './ArticlesListItem';
import articlesReducer, {
  fetchArticles,
  getArticles,
  initialState,
  isFetchComplete,
} from '../../redux/articles';
import fetchData from '../../redux/fetchData';
import { RouteComponentProps } from '@reach/router';

type ArticlesProps = RouteComponentProps<{}>;

function Articles(props: ArticlesProps) {
  const [state, dispatch] = React.useReducer(articlesReducer, initialState);

  React.useEffect(() => {
    fetchData(dispatch, fetchArticles);
  }, []);

  if (!isFetchComplete(state)) {
    return <Loading />;
  }

  const articles = getArticles(state);

  return (
    <section className="articles">
      {articles.map((article) => <ArticlesListItem key={article.title} article={article} />)}
    </section>
  );
}

export default Articles;
