import { Dispatch } from 'react';
import { AxiosMiddlewareAction } from './axiosMiddleware';
import axios, { AxiosResponse } from 'axios';

function fetchData<T>(dispatch: Dispatch<any>, actionCreator: (args?: T) => AxiosMiddlewareAction, actionCreatorParams?: T) {
  const action: AxiosMiddlewareAction = actionCreator(actionCreatorParams);
  const {
    data,
    method = 'get',
    url,
    initActionType,
    successActionType,
    failedActionType,
  } = action.payload;

  // dispatch an action indicating a pending API call
  dispatch({
    type: initActionType,
  });

  // we need to make the call and then dispatch the correct action types
  const requestParams = {
    data,
    method,
    url,
  };

  axios.request(requestParams).then((response: AxiosResponse) => {
    dispatch({
      type: successActionType,
      data: response.data,
    });
  }, (error: any) => {
    dispatch({
      type: failedActionType,
      data,
      error,
    });
  });
}

export default fetchData;